import { Component, OnInit ,ViewChild ,Output } from '@angular/core';
import { ActivatedRoute  , ParamMap , Router} from '@angular/router';
import { dbGET} from '../app-lienhe.service';
import { DemoTestComponent} from '../demo-test/demo-test.component';
import { Obj } from '../obj';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  @ViewChild(DemoTestComponent ,{static : false})
  DemoComponent : DemoTestComponent;
  userID : Obj ;
  id ;
  constructor( private route:ActivatedRoute , private _dbGET : dbGET ,private router: Router) {
  }

  ngOnInit() {
    this.route.paramMap.subscribe((params : ParamMap)=>{
      this.id =  params.get('id').toString();
    });
    this._dbGET.getEmployess().subscribe(data => this.findUser(data,this.id));
  }

  findUser(hello,id){
    for(var i = 0;i<hello.length;i++){
      if(hello[i].id == id){
        this.userID = hello[i];
      }
    }
  }
  updateUser(user){
    console.log('123123');
    for(var i = 0;i<this.DemoComponent.dataService.length;i++){
      if(this.DemoComponent.dataService[i].id == this.id){
        this.DemoComponent.dataService[i] = user;
        console.log("Hello ");
      }
    }
  }
  Hello(){
    console.log(this.id);
    console.log(this.userID);
  }
  onSubmit(formUpdate){
    console.log("Hello2");
    this.updateUser(formUpdate.value);
  }
}
