import { Component, OnInit } from '@angular/core';
import { dbGET }from '../app-lienhe.service';

@Component({
  selector: 'app-demo-test',
  templateUrl: './demo-test.component.html',
  styleUrls: ['./demo-test.component.css']
})
export class DemoTestComponent implements OnInit {

  dataService = []
  constructor( private _dbGET : dbGET ) { }

  ngOnInit() {
    this._dbGET.getEmployess().subscribe(data => this.dataService = data)
  }
  showDB(){
    console.log(this.dataService)
  }
  deleteInfoUser(id){
    var temp = 0;
    for(var i = 0 ;i< this.dataService.length ; i++){
      if(this.dataService[i].id === id){
        temp = i;
      }
    }
    return this.dataService.splice(temp , 1);
  }
}
